<template>
    <div class="btn-container">
        <div class="btn download">
            <span ref="copy" :data-clipboard-text="copyValue" @click="goApp">App内下载</span>
        </div>
        <div class="btn save">
            <span @click="saveToCloud">转存到网盘</span>
        </div>
    </div>
</template>

<script>
import Clipboard from 'clipboard'
 import { isQQBrowser } from '../test/result'
 import { _openApp } from '../utils/app'
export default {
  name: "saveButton",
  props: {
  },
  data() {
    return {
      copyValue: '',
      copyBtn: '',
      outlinkUrl: 'http://caiyun.feixin.10086.cn/dl/'
    }
  },
  computed: {

  },
  mounted() {
    // let base64Info = this.$router.query.base64Info
    // console.log(`from router ${base64Info}`);
    if(isQQBrowser()) {
      this.copyBtn = new Clipboard(this.$refs.copy)
    }
  },
  methods: {
    saveToCloud() {
      this.$emit("saveToCloud");
    },
    goApp() {
      if(isQQBrowser()) {
        let linkID ='185CkdO1Y7L7W'
        let code = 'inx2Ot'
          this.copyValue = `链接：${this.outlinkUrl}${linkID} 提取码：${code}`
          const clipboard = this.copyBtn
          clipboard.on('success',function() {
            _openApp()
          })
          clipboard.on('error',function() {
            console.log('error');
          })
      } else {
        console.log('noqq');
        this.$emit("goApp");
      }
    }
  },
  destroy() {
    this.copyBtn.destroy()
  }
};
</script>

<style scoped>
    .btn-container {
        position: fixed;
        width: 80%;
        z-index: 1;
        bottom: 40px;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
      }
      .btn {
          width: 100px;
          height: 40px;
          line-height: 40px;
          font-size: 18px;
          border: 1px solid blue;
          border-radius: 10px;
          text-align: center;
      }
      .download {
            background-color: #fff;
            color: blue;
            box-shadow: 0 6px 12px rgba(0,0,0,0.5);
        }
        .save {
            background-color: blue;
            color: #fff;
            box-shadow: 0 6px 12px rgba(94,136,155,0.8);
        }
</style>

  </div>
</template>



